html {
  font-size: 62.5%;
  box-sizing: border-box; }

* {
  margin: 0;
  padding: 0; }

*, *::before, *::after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

.hamburguer-menu:hover {
  cursor: pointer; }

.bg-green {
  background-color: #4CB8B3; }

.bg-yellow {
  background-color: #FEF46D; }

.bold {
  font-weight: 700; }

.container {
  max-width: 120rem;
  margin: 0 auto; }

.header {
  width: 100%;
  height: 14rem;
  padding: 0 1rem;
  background-color: #4CB8B3;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .header .header__title p {
    font-size: 2.7rem;
    font-weight: 700;
    color: white; }
  .header .header__nav {
    display: flex;
    flex-direction: column;
    align-items: flex-end; }
    .header .header__nav a {
      text-decoration: none;
      color: white;
      font-size: 1.7rem;
      margin-bottom: 1rem; }
      .header .header__nav a:hover {
        color: #FEF46D; }

@media (min-width: 551px) {
  .header {
    justify-content: space-evenly; }
  .header__nav {
    margin-right: 2rem; } }

.video-container {
  min-height: 40rem;
  max-height: 70rem;
  position: relative;
  overflow: hidden; }
  .video-container .video {
    display: block; }
  .video-container .video__title {
    position: absolute;
    background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 155, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 2rem; }
    .video-container .video__title h1 {
      color: white;
      font-size: 4rem; }
    .video-container .video__title p {
      background-color: #F53756;
      color: white;
      display: inline;
      padding: 1rem;
      font-size: 2rem;
      font-weight: 700;
      width: 60%;
      max-width: 38.7rem; }

.banner {
  padding: 2rem 0; }
  .banner .banner__image {
    display: flex;
    justify-content: center;
    width: 100%; }
    .banner .banner__image img {
      width: 100%;
      max-width: 45rem;
      margin: 0 auto; }
  .banner .banner__description .description__title {
    font-size: 2.5rem;
    margin: 2rem 0;
    text-align: center; }
  .banner .banner__description .description__date {
    color: #F53756;
    font-weight: 700;
    font-size: 1.5rem;
    text-align: center;
    margin-bottom: 2rem; }
  .banner .banner__description .description__text {
    font-size: 1.5rem;
    line-height: 1.5;
    text-align: justify;
    margin: 0 1rem; }

.line-up {
  background-color: #F53756;
  padding: 2rem; }
  .line-up h3 {
    color: white;
    text-align: center; }
  .line-up .day {
    color: white;
    font-size: 1.5rem;
    text-align: center;
    text-transform: uppercase;
    margin-top: 1rem; }

.bands {
  margin: 2rem auto;
  text-align: center;
  font-size: 1.4rem;
  padding: 2rem 0;
  max-width: 55rem; }
  .bands p {
    padding: 2rem; }
    .bands p span {
      font-weight: 700; }
    .bands p:first-of-type {
      font-size: 1.6rem; }

.bg-yellow p:nth-child(2n) {
  background-color: #fef03a; }

.bg-green p:nth-child(2n) {
  background-color: #3b9691; }

.day-band .bands__title::after {
  content: '';
  height: 10rem;
  display: block;
  margin: 1rem 0;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center; }

.rock .bands__title::after {
  background-image: url(../../biuld/img/icono_rock.png);
  background-position: center center; }

.edm .bands__title::after {
  background-image: url(../../biuld/img/icono_edm.png); }

.gallery {
  width: 100%; }
  .gallery h3 {
    margin: 3rem 0;
    text-align: center; }
  .gallery .photo {
    display: flex;
    justify-content: center; }
    .gallery .photo img {
      width: 100%;
      max-width: 20rem;
      margin: 2rem 0; }

@media (min-width: 551px) {
  .photos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1rem; }
    .photos img {
      width: 25rem; } }

.tickets {
  padding: 3rem 0;
  background-color: #4CB8B3; }
  .tickets h3 {
    color: white;
    text-align: center;
    margin-bottom: 2.5rem; }
  .tickets .table {
    max-width: 38rem;
    margin: 0 auto; }
    .tickets .table .table__title {
      font-size: 1.7rem;
      text-align: center;
      padding: 2rem 0; }
  .tickets .table-prices .table__title {
    background-color: #F53756;
    text-transform: uppercase;
    color: white;
    font-size: 2rem;
    padding: 1rem 0;
    text-align: center; }
  .tickets .table-prices .table__feature {
    padding: 1rem 0;
    background-color: white;
    font-size: 1.7rem;
    text-align: center;
    border-bottom: 1px solid #efefef; }
  .tickets .table-prices .table__price {
    padding: 1rem 0;
    font-size: 2rem;
    text-align: center;
    background-color: #752F97;
    color: white; }

.footer {
  padding: 2.5rem 0;
  background-color: #F53756; }
  .footer p {
    font-size: 1.5rem;
    color: white;
    text-align: center; }
